// @flow

import importAll from 'import-all.macro';
import {hostname, description, owner} from '../config';
import {postMeta} from '../utilities';
import Layout from '../components/application';
import Card, {Cards} from '../components/blog/card';
import WelcomeBanner from '../components/welcome-banner';
import Head from 'next/head';

const prefixes = ['', 'dc:', 'og:', 'twitter:'];

function Seo({title, description, author}) {
    return (
        <Head>
            {title && (
                <>
                    <title>{title}</title>
                    <meta name="dc:title" content={title} />
                </>
            )}
            {author && (
                <>
                    <meta name="author" content={author} />
                    <meta name="dc:creator" content={author} />
                </>
            )}
            {description &&
                prefixes.map(prefix => (
                    <meta
                        key={prefix}
                        name={`${prefix}description`}
                        content={description}
                    />
                ))}
        </Head>
    );
}

const posts = Object.entries(importAll.sync('./blog/*.mdx'))
    .reduce(
        (arr, [path, exports]: [string, any]) =>
            arr.concat([postMeta({...exports.meta, path})]),
        []
    )
    .sort((p1, p2) => p2.date - p1.date);

export default function Page() {
    return (
        <Layout>
            <Seo
                title={`${hostname} &nbsp;&bull;&nbsp; homepage`}
                description={excerpt}
            />
            <Head>
                <title>{hostname} &nbsp;&bull;&nbsp; homepage</title>
                <meta
                    name="dc:title"
                    content={`${hostname} &nbsp;&bull;&nbsp; homepage`}
                />
                <meta name="dc:description" content={excerpt} />
                <meta name="author" content={owner.fullName} />
                <meta name="description" content={description} />
            </Head>
            <WelcomeBanner />
            <Cards>
                {posts.map(post => (
                    <Card key={post.title} {...post} />
                ))}
            </Cards>
        </Layout>
    );
}
